import Token from "./Token";
export default function groupTextTokens(tokens) {
  var result = [];
  var hasGroup = false;
  tokens.forEach(function (token, index) {
    if (!token.block && !hasGroup) {
      hasGroup = true;
      result.push(new Token('textgroup', 1));
      result.push(token);
    } else if (!token.block && hasGroup) {
      result.push(token);
    } else if (token.block && hasGroup) {
      hasGroup = false;
      result.push(new Token('textgroup', -1));
      result.push(token);
    } else {
      result.push(token);
    }
  });
  return result;
}